body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.header {
  text-align: center;
  background-color: #06062f;
  padding: 20px;
}

.search-container {
  margin-bottom: 10px;
}

.search-form input[type="text"] {
  -webkit-transition: all .5s;
  border: 5px solid red;
  border-radius: 20px;
  margin-right: 10px;
  padding: 10px;
  transition: all 2s;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  display: flex;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 300px;
  margin: 10px;
  padding: 10px;
}

.photo-card img {
  border-radius: 5px;
  width: 100%;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.load-more:hover, .search-form button:hover {
  color: #000;
  background-color: #4caf50;
  transition: all .8s;
}

input[type="text"]:focus {
  border: 5px solid #4caf50;
  border-radius: 20px;
}
